import { Component } from '@angular/core';
import { AlertController, IonicPage, LoadingController, NavController, NavParams } from 'ionic-angular';

import { AngularFireAuth } from 'angularfire2/auth';

import { CartService, CompanyService, ProductService } from '../../services';
import { ICompany, IProductExisting } from '../../interfaces';

import { first } from 'rxjs/operators';

@IonicPage({
  segment: 'product/:productKey'
})
@Component({
  selector: 'page-product',
  templateUrl: 'product.html',
})
export class ProductPage {
  loading: any;
  showPage:boolean;

  title: string;
  uid: string;

  company:ICompany;
  product: IProductExisting;
  isProductInCart: any;

  productKey: string;

  constructor(
    public alertCtrl: AlertController,
    public angularFireAuth: AngularFireAuth,
    public cartService:CartService,
    public companyService:CompanyService,
    public loadingCtrl: LoadingController,
    public navCtrl: NavController,
    public navParams: NavParams,
    public productService: ProductService
  ) {
    this.productKey = this.navParams.get('productKey');
  }

  ionViewDidLoad(){
    this.angularFireAuth
      .authState
      .pipe(first())
      .subscribe(user => {
        if(!user){ return; }

        this.uid = user.uid;
      }, error => console.error(error));
  }

  ionViewDidEnter(){
    this.loading = this.loadingCtrl.create({
      content: 'Aguarde...',
      dismissOnPageChange: true
    });

    this.loading.present();

    this.getProduct();
    this.checkIfProductIsInCart();
  }

  private getProduct(){
    this.productService
      .getOne(this.productKey)
      .pipe(first())
      .subscribe( product => {
        this.product = product;
        this.getCompany();
      }, error => {
        console.error(error);
      });
  }

  private getCompany(){
    this.companyService
      .getOne(this.product.companyKey)
      .pipe(first())
      .subscribe((company:ICompany) => {
        if(!company){ return; }

        this.company = company;

        this.showPage = true;
        this.loading.dismiss();
      }, error => {
        this.loading.dismiss();
        console.error(error);
      });
  }

  // Ações
  // ***********************************
  editProduct(){
    this.navCtrl.push('ProductEditPage',{
      productKey: this.productKey
    });
  }

  removeProduct(){
    const confirm = this.alertCtrl.create({
      subTitle: 'Remover produto?',
      buttons: [
        {
          text: 'Não'
        },
        {
          text: 'Remover',
          handler: () => {
            this.productService
              .removeOne(this.productKey)
              .then( () => {
                this.navCtrl.getPrevious().data.companyKey = this.company.key;
                this.navCtrl.pop();
              });
          }
        }
      ]
    });
    confirm.present();
  }

  // Cart
  // ***********************************
  addProductToCart(){
    this.cartService
      .addOneSku(this.productKey)
      .then(() => {
        this.isProductInCart = true;
      }, error => console.error(error));
  }

  private checkIfProductIsInCart(){
    this.cartService
      .isSkuinCart(this.productKey)
      .then(isProductInCart => {
        this.isProductInCart = isProductInCart;
      }, error => console.error(error));
  }

  removeProductFromCart(){
    this.cartService
      .removeOneSku(this.productKey)
      .then(() => {
        this.isProductInCart = false;
      }, error => console.error(error));
  }
}



